<template >
  <div class="container">
    <div v-show="!userID.issue.length" class="add">
      <a class="btn btn-primary" v-link="{name: 'addIssue', query: {userID: 'admin'}}" role="button">
        <span class="glyphicon glyphicon-plus" aria-hidden="true"></span> 
      </a>
    </div>
    <div class="list" v-show="userID.issue.length">
      <table class="table ">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th>
            <a class="btn btn-warning button-rig" v-link="{name: 'addIssue', query: {userID: 'admin'}}" role="button">
              <span class="glyphicon glyphicon-plus" aria-hidden="true"></span> 
            </a>
          </th>
        </tr>
        <tr v-for="issueList of userID.issue" v-if="issueList.del" class="tr-hover">
          <td class="riaList-checkbox">
            <input type="checkbox" :value="issueList.id" v-model="checked">
          </td>
          <td class="riaList-title">{{ issueList.title }}</td>
          <td> {{ issueList.addTime }} </td>
          <td v-bind:class="{ 'state-a': pubState($index) === '', 'state-b': pubState($index) === '' }">
            {{ pubState($index) }}
          </td>
          <td>
            <a class="btn btn-primary" v-if="pubState($index) !== ''" v-link="{name: 'addIssue', query: {userID: 'admin',issueListId: issueList.id}}" role="button"></a>
            <a class="btn btn-success" v-if="pubState($index) !== ''" role="button" v-link="{name: 'questionnaire', query: {id: issueList.questionnaireID}}"></a>
            <a class="btn btn-info" v-link="{name: 'getData', query: {id: issueList.questionnaireID}}" role="button" v-if="pubState($index) !== ''"></a>
            <!--   -->
            <!-- <a class="btn btn-warning" v-if="pubState($index) === ''"
              v-link="" role="button"></a> -->
            <button type="button" class="btn btn-danger" @click=" issueId = issueList.id " data-toggle="modal" data-target="#myModal2">
              
            </button>
          </td>
        </tr>
      </table>
      <div class="all-del">
        <input type="checkbox" v-model="allChecked">
        <label for="del"></label>
        <button type="button" class="btn btn-danger" data-toggle="modal" data-target="#myModal"></button>
      </div>
    </div>
    <!-- Modal- -->
    <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
      <div class="modal-dialog modal-sm" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
            <h4 class="modal-title" id="myModalLabel"></h4>
          </div>
          <div class="modal-body" v-if="checked.length"></div>
          <div class="modal-body" v-if="!checked.length"></div>
          <div class="modal-footer">
            <button type="button" class="btn btn-default" data-dismiss="modal" @click="allDelList()"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal- -->
    <div class="modal fade" id="myModal2" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
      <div class="modal-dialog modal-sm" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
            <h4 class="modal-title" id="myModalLabel"></h4>
          </div>
          <div class="modal-body">
            
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-default" data-dismiss="modal" @click="delIssye(issueId)"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      checked: [],   // 
      issueId: null, // 
      userID: {      // 
        issue: []
      }
    }
  },
  created: function () {
    let data = JSON.parse(global.localStorage.getItem('issueDate'))
    if (data) {
      for (var i = 0; i < data.issueList.length; i++) {
        let issueTemp = {}
        issueTemp.id = data.issueList[i].issueListId
        if (data.issueList[i].itemTitle === '' || !data.issueList[i].itemTitle) {
          issueTemp.title = ''
        } else {
          issueTemp.title = data.issueList[i].itemTitle
        }
        issueTemp.addTime = data.issueList[i].addTime
        issueTemp.endTime = data.issueList[i].endDate
        issueTemp.state = data.issueList[i].state ? data.issueList[i].state : ''
        issueTemp.questionnaireID = data.issueList[i].questionnaireID
        issueTemp.del = true
        this.userID.issue.push(issueTemp)
      }
    }
  },
  methods: {
    // 
    pubState: function (index) {
      if (this.userID.issue[index].state !== '' && this.userID.issue[index].endTime) {
        if (new Date(this.userID.issue[index].endTime) < new Date()) return ''
      }
      return this.userID.issue[index].state
    },
    // 
    delIssye: function (index) {
      let data = JSON.parse(global.localStorage.getItem('issueDate'))
      let qData = JSON.parse(global.localStorage.getItem('questionnaireData'))
      // 
      for (var i = 0; i < this.userID.issue.length; i++) {
        if (this.userID.issue[i].id === index) {
          this.userID.issue.splice(i, 1)
        }
      }
      // 
      for (var j = 0; j < data.issueList.length; j++) {
        if (data.issueList[j].issueListId === index) {
          data.issueList.splice(j, 1)
          global.localStorage.setItem('issueDate', JSON.stringify(data))
        }
      }
      for (var k = 0; k < qData.length; k++) {
        if (qData[k].questionnaireId === this.userID.issue.questionnaireID) {
          qData.splice(k, 1)
          global.localStorage.setItem('questionnaireData', JSON.stringify(qData))
        }
      }
    },
    // 
    allDelList: function () {
      for (var i = 0; i < this.checked.length; i++) {
        console.log(this.checked[i])
        this.delIssye(this.checked[i])
      }
    }
  },
  computed: {
    // 
    allChecked: {
      get: function () {
        return this.checked.length === this.userID.issue.length
      },
      set: function (value) {
        if (value) {
          this.checked = this.userID.issue.map(function (issueList) {
            return issueList.id
          })
        } else {
          this.checked = []
        }
      }
    }
  }
}
</script>

<style lang="css" scoped>
.container{
  background-color: #fff;
  box-shadow: 0 0  6px 2px #999;
  padding: 10px;
  margin: 20px auto;
}
.add{
  width: 100%;
  height: 300px;
  position: relative;
}
.add a{
  width: 150px;
  height: 40px;
  border: none;
  background-color: rgb(249, 138, 22);
  outline: none;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -20px 0 0 -75px;
  font-size: 16px;
  color: #f5f5f5;
  line-height: 16px;
  padding: 0;
  line-height: 40px;
}
.add a .glyphicon{
    margin-right: 10px;
    font-size: 18px;
}
.add a:hover{
   background-color: rgb(245, 94, 75);
}
.list{
  padding: 5px 10px;
}
.button-rig{
  float: right;
}
.table, .table th{
  text-align: center;
}
.table td{
  vertical-align:middle;
}
.state-a{
  color: rgb(84, 224, 102);
}
.state-b{
  color: rgb(222, 51, 45);
}
.riaList-title{
  width: 30%;
}
.all-del{
  padding: 5px 0 30px 10px;
}
.all-del button{
  margin-left: 20px;
}
.tr-hover:hover{
  background-color: #fcede1;
}
</style>



