//  Bootstrap
import './assets/lib/css/bootstrap.css'
import './assets/lib/css/date.css'
import './assets/lib/js/bootstrap'
import Vue from 'vue'
import App from './App'
import Router from 'vue-router' // vue-router
import routerMap from './router' // 
/* eslint-disable no-new */
new Vue({
  el: '#root',
  components: { App }
})
Vue.use(Router) // vue-router
const router = new Router() // 
routerMap(router) // 
router.start(App, '#root') // 



