export default function (router) {
  router.map({
    '/': {
      name: 'index',
      component: require('./components/home.vue')
    },
    '/add': {
      name: 'addIssue',
      // 
      component: function (resolve) {
        require(['./components/addIssue.vue'], resolve)
      }
    },
    '/data': {
      name: 'getData',
      // 
      component: function (resolve) {
        require(['./components/getData.vue'], resolve)
      }
    },
    '/questionnaire': {
      name: 'questionnaire',
      // 
      component: function (resolve) {
        require(['./components/questionnaire.vue'], resolve)
      }
    },
    '/succes': {
      name: 'succes',
      // 
      component: function (resolve) {
        require(['./components/succes.vue'], resolve)
      }
    }
  })
}



